<template>
  <div class="yandexmarket-field-value">
    <v-card-title class="pa-0 flex-nowrap">
      <template v-if="isCurrencies(field)">
        <v-select
            :value="field.value"
            :items="field.values"
            filled
            dense
            multiple
            solo
            chips
            deletable-chips
            prepend-inner-icon="icon-tags"
            small-chips
            hide-details="auto"
            item-value="value"
            item-text="text"
            @change="changedValue"
            :menu-props="{offsetY: true}"
            :attach="true"
        >
          <template v-slot:selection="{ attrs, index, item, selected }">
            <v-chip
                v-bind="attrs"
                :input-value="selected"
                text-color="white"
                :color="!index ? 'primary' : 'grey'"
                :title="!index ? ' ' : ' '"
                @click.stop="makeFirst(item)"
                @click:close="removeChip(item)"
                close
                small
            >
              <strong v-if="item.value">{{ item.value }}</strong>
              <span class="pl-1" v-if="item.text">({{ item.text }})</span>
              <span v-else>{{ item }}</span>
            </v-chip>
          </template>
        </v-select>
      </template>
      <template v-else-if="isSimpleString(field)">
        <v-text-field
            :value="field.value"
            @input="changedValue"
            label=" "
            placeholder="  XML     "
            hide-details
            solo
            dense
        >
          <template v-slot:prepend-inner>
            <div class="text-no-wrap mr-2">
              <v-icon left color="inherit">icon-paragraph</v-icon>
              <code style="position:relative; top:1px;"></code>
            </div>
          </template>
        </v-text-field>
      </template>
      <template v-else>
        <v-combobox
            :value="value"
            @input="changedValue"
            :filter="valueSearch"
            :items="dataColumns"
            :attach="true"
            label="  "
            placeholder='    "Class.key"   Enter'
            item-value="value"
            item-text="text"
            hide-details
            solo
            dense
            clearable
        >
          <template v-slot:prepend-inner>
            <div class="text-no-wrap mr-2">
              <v-icon left color="inherit">icon-list-ul</v-icon>
              <code style="position:relative; top:1px;">{{ valuePrepend }}</code>
            </div>
          </template>
          <template v-slot:item="{item}">
            <code>{{ item.value }}</code>
            <span class="pl-1">{{ item.text }}</span>
            <small v-if="item.help" class="pl-1 grey--text">&nbsp;({{ item.help }})</small>
          </template>
        </v-combobox>
        <v-text-field
            v-if="isPicture(field)"
            :value="field.properties.count"
            @input="countChanged"
            type="number"
            class="ml-2"
            title="     (10 )"
            min="0"
            max="10"
            label="-"
            style="max-width: 100px"
            solo
            dense
            hide-details
        >
        </v-text-field>
        <v-btn v-else
               :title="openedCode ? '    ' :' - '"
               :color="openedCode ? 'secondary' : 'accent'"
               @click="toggleCode"
               class="ml-3"
               min-width="30"
               elevation="0">
          <v-icon>icon-code</v-icon>
        </v-btn>
      </template>
    </v-card-title>
    <v-sheet elevation="1" v-if="openedCode" class="mt-2" style="position: relative;">
      <vue-codemirror
          v-if="rerender"
          v-model="field.handler"
          :options="cmOptions"
          placeholder=": {$input === '' ? true : false}"
      ></vue-codemirror>
      <v-tooltip left :max-width="450" :close-delay="200" :attach="true">
        <template v-slot:activator="{on}">
          <v-btn v-on="on" x-small fab icon absolute style="right: -4px; top: -4px;">
            <v-icon color="grey darken-1">icon-question-circle</v-icon>
          </v-btn>
        </template>
        <div class="text-caption" style="white-space: pre-line;">
          INLINE    Fenom (   $input)<br>
              boolean,   /,  , -  
           .
          <br><br>
             {$resource.pagetitle},  miniShop2 {$data.price},  ms2 {$option.color},
          
           {$tv.tag}.<br>
            -,    .<br>
          <br>
           @INLINE    .
        </div>
      </v-tooltip>
    </v-sheet>
  </div>
</template>

<script>
import {codemirror} from 'vue-codemirror';
import {mapGetters} from 'vuex';

export default {
  name: 'FieldValue',
  props: {
    field: {type: Object, required: true},
  },
  components: {
    VueCodemirror: codemirror
  },
  data: () => ({
    rerender: true,
    code: false,
    cmOptions: {
      taSize: 4,
      mode: {
        name: 'smarty',
        baseMode: 'text/html',
        version: 3,
      },
      line: true,
      lineNumbers: true,
      lineWrapping: true
    },
  }),
  computed: {
    ...mapGetters(['dataColumns', 'columnText']),
    ...mapGetters('field', [
      'isSimpleString',
      'isCurrencies',
      'isCategories',
      'isOffers',
      'isShop',
      'isPicture'
    ]),
    value() {
      if (typeof this.field.value === 'object') {
        return this.field.value;
      }
      return {
        value: this.field.value,
        text: this.columnText(this.field.value) || this.field.value
      };
    },
    valuePrepend() {
      if (this.value && this.value.value !== this.value.text) {
        return this.value.value;
      }
      return '';
    },
    openedCode() {
      return this.field.handler || this.code;
    },
  },
  methods: {
    valueSearch(item, queryText, itemText) {
      return itemText.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase()) > -1
          || (item && item.value && item.value.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase()) > -1);
    },
    changedValue(val) {
      let value;
      if (val === null) {
        value = null;
      } else if (Array.isArray(val)) {
        value = val;
      } else if (typeof val === 'object') {
        value = val.value;
      } else { //  
        value = val;
      }
      this.$emit('input', value);
      // if it will be wrong, see https://github.com/vuetifyjs/vuetify/issues/5479#issuecomment-672300135
    },
    toggleCode() {
      if (this.field.handler) {
        return;
      }
      this.code = !this.code;
    },
    removeChip(item) {
      let values = [...this.field.value];
      values.splice(values.indexOf(item.value), 1)
      this.changedValue(values);
    },
    makeFirst(item) {
      let values = [...this.field.value];
      values.splice(values.indexOf(item.value), 1);
      values.unshift(item.value);
      this.changedValue(values);
    },
    countChanged(count) {
      let properties = this.field.properties;
      properties.count = count ? parseInt(count) : 0;
      this.$set(this.field, 'properties', properties)
    }
  },
  mounted() {
    this.rerender = false;
    this.$nextTick().then(() => this.rerender = true);
    this.code = !!this.field.handler;
  }
}
</script>

<!--suppress CssUnusedSymbol -->
<style>
.yandexmarket-field-value .CodeMirror {
  height: auto;
  min-height: 30px;
}

.yandexmarket-field-value .CodeMirror pre.CodeMirror-placeholder {
  color: #999;
}

</style>