import Vue from 'vue'
import Vuex from 'vuex'
import field from './modules/field'
import pricelist from './modules/pricelist'
import marketplace from './modules/marketplace'
import api from "@/api";

Vue.use(Vuex);

export default new Vuex.Store({
    modules: {
        field,
        marketplace,
        pricelist,
    },
    state: {
        classKeys: [],
        operators: [
            {text: '', sign: '=', value: 'equals', select: true},
            {text: ' ', sign: '!=', value: 'not equals', select: true},
            {text: ' ', sign: '>', value: 'greater than'},
            {text: ' ', sign: '<', value: 'less than'},
            {text: '  ', sign: '>=', value: 'greater than or equal to'},
            {text: '  ', sign: '<=', value: 'less than or equal to'},
            {text: ' ', sign: 'LIKE', value: 'like'},
            {text: '  ', sign: 'NOT LIKE', value: 'not like'},
            {text: ' ', sign: 'IN', value: 'exists in', multiple: true, select: true},
            {text: '  ', sign: 'NOT IN', value: 'not exists in', multiple: true, select: true},
            {text: 'Null-', sign: 'IS NULL', value: 'is null', valueless: true},
            {text: ' null', sign: 'IS NOT NULL', value: 'is not null', valueless: true},
        ],
    },
    getters: {
        dataColumns: state => state.classKeys.filter(ck => !ck.skipped),
        operatorsList: state => state.operators,
        columnText: state => value => {
            let found = state.classKeys.find(classKey => classKey.value === value);
            return found ? found.text : null;
        }
    },
    mutations: {
        setClassKeys(state, payload) {
            state.classKeys = payload.results;
        },
    },
    actions: {
        loadClassKeys({commit}) {
            api.post('lists/classkeys')
                .then(({data}) => commit('setClassKeys', data))
        },
    }
});