<template>
  <v-row class="yandexmarket-pricelist-generate">
    <v-col cols="12" md="6">
      <h4>   -</h4>
      <p class="mb-2">    </p>
      <v-select
          filled
          label=" - (  )"
          dense
          disabled
          v-model="data.type"
          :items="marketplaces"
      ></v-select>
      <v-text-field
          filled
          label=" -"
          dense
          v-model="data.name"
      ></v-text-field>
      <v-text-field
          filled
          dense
          class="mb-1"
          label="  (    xml)"
          :hint="`     ${pricelist.path}`"
          v-model="data.file"
      ></v-text-field>
      <v-select
          filled
          dense
          class="mb-2"
          label="  "
          v-model="data.generate_mode"
          :items="modes"
          :hint="modeHint"
          :persistent-hint="!!modeHint"
          :menu-props="{offsetY: true}"
          :attach="true"
      ></v-select>
      <v-text-field
          v-if="data.generate_mode"
          filled
          :min="0"
          dense
          label="   N ,     "
          hint="0 -   .        1440"
          v-model="data.generate_interval"
          type="number"
      ></v-text-field>
      <v-checkbox v-model="data.active" label="     " hide-details
                  dense
                  class="mt-0 mb-2"/>
      <v-card-actions class="px-0">
        <v-btn small v-if="hasChanges" @click="cancelChanges" title="  ">
          <v-icon left>icon-undo</v-icon>
           
        </v-btn>
        <v-spacer/>
        <v-btn :disabled="!hasChanges" @click="saveChanges" color="secondary" small>
          <v-icon left small>icon-save</v-icon>
          
        </v-btn>
      </v-card-actions>
      <template v-if="rootField">
        <h4 class="mb-1">   </h4>
        <v-expansion-panels v-model="openedFields" multiple class="pb-2" key="offers">
          <pricelist-field
              :readonly="false"
              :item="rootField"
              :fields="pricelist.fields"
              :attributes="pricelist.attributes"
              :lighten="3"
              v-on="$listeners"
              :available-fields="[]"
              :available-types="[]"
              parent="root"
          />
        </v-expansion-panels>
      </template>
    </v-col>
    <v-col cols="12" md="6">
      <v-alert v-if="pricelist.need_generate" type="warning" dense>
         .   !
      </v-alert>
      <v-alert v-if="pricelist.generated_on" type="info" color="accent" dense>
         -  {{ generatedOn }}
      </v-alert>
      <p v-if="pricelist.generated_on">  :
        <a :href="pricelist.fileUrl" title="    " target="_blank">{{ pricelist.fileUrl }}</a>
      </p>
      <h4 v-else class="mb-3">-      </h4>
      <v-btn @click="generateFile" :disabled="loading" color="secondary" class="mb-3"
             title="   ">
          
      </v-btn>
      <v-textarea
          class="yandexmarket-textarea-log mt-3"
          auto-grow
          outlined
          label=" "
          :value="log"
          placeholder="    "
          disabled
      />
    </v-col>
  </v-row>
</template>

<script>
import {mapState} from 'vuex';
import api from "@/api";
import PricelistField from "@/components/PricelistField";

export default {
  name: 'PriceListGenerate',
  props: {
    pricelist: {required: true, type: Object}
  },
  components: {
    PricelistField
  },
  data: () => ({
    openedFields: [],
    data: {},
    loading: false,
    log: '    ',
    modes: [
      {
        value: 0,
        text: '   - (cron )',
        hint: '    '
      },
      {
        value: 1,
        text: '     (  )',
        hint: '-   .    cron'
      },
      {
        value: 2,
        text: ' cron    ()',
        hint: '  -  /core/components/yandexmarket/cron.php'
      },
    ],
  }),
  watch: {
    pricelist: {
      immediate: true,
      // eslint-disable-next-line no-unused-vars
      handler: function ({fields, categories, attributes, conditions, ...data}) {
        this.data = {...this.data, ...data};
      }
    }
  },
  computed: {
    ...mapState('marketplace', ['marketplaces']),
    rootField() {
      return this.pricelist.fields.find(field => field.type === 1);
    },
    generatedOn() {
      return this.pricelist.generated_on.date.replace('.000000', '');
    },
    modeHint() {
      let mode = this.modes.find(m => m.value === parseInt(this.data.generate_mode));
      if (mode) {
        return mode.hint;
      }
      return '';
    },
    hasChanges() {
      // eslint-disable-next-line no-unused-vars
      let {fields, categories, attributes, conditions, ...data} = this.pricelist;
      return JSON.stringify(this.data) !== JSON.stringify(data);
    }
  },
  methods: {
    cancelChanges() {
      // eslint-disable-next-line no-unused-vars
      let {fields, categories, attributes, conditions, ...data} = this.pricelist;
      this.data = {...this.data, ...data};
    },
    saveChanges() {
      this.$emit('pricelist:updated', {...this.data});
    },
    generateFile() {
      if (!this.pricelist.generated_on || confirm('    ?    ')) {
        this.loading = true;
        api.post('xml/generate', {id: this.pricelist.id})
            .then(({data}) => {
              this.log = data.message;
              this.$emit('pricelist:updated', {...data.object}, false);
            })
            .catch(error => console.error(error))
            .then(() => this.loading = false);
      }
    }
  }
}
</script>

<style>
.yandexmarket-pricelist-generate .yandexmarket-textarea-log textarea {
  font-family: monospace;
  color: #999 !important;
  font-size: 0.875em;
  line-height: 1.25rem;
}
</style>